export const moneyItemAdapter = (
  price,
  locales = "vn-VN",
  currency = "VND"
) => {
  if (!price) {
    return "0 ƒë";
  }
  return (
    new Intl.NumberFormat(locales, {
      currency: currency,
    }).format(price) + " ƒë"
  );
};

export const dateTimeAdapter = (time) => {
  return moment(time).format("HH:mm:ss DD/MM/YYYY");
};

